.Track {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(256, 256, 256, 0.8);
  }
  
  .Track-action {
    cursor: pointer;
    padding: .5rem;
    font-size: 1.05rem;
    transition: color .25s;
    border: 0px;
    background-color: rgba(0, 0, 0, 0);
    color: #fff;
  }
  
  .Track-action:hover {
    color: rgba(265, 265, 265, .5);
  }
  
  .Track-information {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 72px;
  }
  
  .Track-information h3 {
    margin-bottom: .22rem;
  }
  
  .Track-information p {
    font-size: .83rem;
    font-weight: 300;
    color: rgba(256, 256, 256, 0.8);
  }

  .addButton {
    margin-left: 1rem;
    padding: 0.5rem;
    font-size: 1.5rem;
    background-color: rgba(108, 65, 233, .7);
    color: white;
    border: none;
    border-radius: 50%;
    cursor: pointer;
}

.addButton:hover {
    background-color: #9369b8;}

.container {
      position: relative;
      width: 100%;
      height: 100vh;
      overflow: hidden;
      
  }

 .fixButton {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }